.card-container {
    display:flex;
    flex-direction: column;
    background: #95dada;
    border: 1px solid grey;
    border-radius: 5px;
    padding: 2em .5em;
    cursor:pointer;
    -moz-osx-font-smoothing:grayscale;
    backface-visibility: hidden;
    transform: translateZ(0);
    transition: transform .25s ease-in-out;
}

.card-container:hover{
    transform:scale(1.05)
}
